.login {
  display: grid;
  place-items: center;
  margin-left: auto;
  margin-right: auto;
  padding-top: 100px;
  padding-bottom: 100px;
}

.login img {
  object-fit: contain;
  height: 70px;
  margin-top: 20px;
  margin-bottom: 20px;
}

.login form {
  display: flex;
  flex-direction: column;
}

.login form input {
  width: 365px;
  height: 50px;
  font-size: 20px;
  padding-left: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
}

.login form button {
  width: 365px;
  height: 50px;
  font-size: large;
  color: #fff;
  background: #0074b1;
  border-radius: 5px;
  cursor: pointer;
}

.login p {
  margin-top: 20px;
}

.login span {
  color: #0177b7;
  cursor: pointer;
  padding-left: 5px;
}