.sidebar {
  position: sticky;
  margin-top: 20px;
  flex: 0.2;
  border-radius: 10px;
  text-align: center;
  height: fit-content;
}

.sidebar__top {
  display: flex;
  flex-direction: column;
  align-items: center;
  border: 1px solid lightgray;
  border-bottom: 0;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  background: white;
}

.sidebar__top img {
  margin-bottom: -20px;
  width: 100%;
  height: 60px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  object-fit: cover;
}

.sidebar__top h2 {
  font-size: 18px;
}

.sidebar__top h4 {
  font-size: 12px;
  color: gray;
  padding: 5px 10px;
}

.avatar {
  width: 40px;
  height: 40px;
  text-align: center;
  margin-bottom: 10px;
  background: #ccc;
  color: white;
  padding: 12px;
  border-radius: 50%;
}

.sidebar__stats {
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid lightgray;
  background: white;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.sidebar__stat {
  margin-top: 10px;
  display: flex;
  justify-content: space-between;
}

.sidebar__stat p {
  color: gray;
  font-size: 13px;
  font-weight: 600;
}

.sidebar__stat span {
  color: #0a66c2 !important;
  font-size: 13px;
  font-weight: bold;
}

.sidebar__bottom {
  text-align: left;
  padding: 10px;
  border: 1px solid lightgray;
  background: white;
  border-radius: 10px;
  margin-top: 10px;
}

.recentItem {
  display: flex;
  font-size: 13px;
  color: gray;
  font-weight: bolder;
  cursor: pointer;
  padding: 3px 0px;
}

.recentItem span {
  margin: 0px 4px;
}

.recentItem:hover {
  background: whitesmoke;
  border-radius: 10px;
  cursor: pointer;
  color: black;
}